.hi-john,
.where-do-you {
  position: relative;
  display: inline-block;
  flex-shrink: 0;
}
.hi-john {
  font-weight: 300;
  width: 137px;
  height: 32px;
}
.where-do-you {
  font-size: 41.39px;
  line-height: 137.14%;
  color: var(--high-emphasized);
  width: 341px;
  height: 49.14px;
  margin-top: -10px;
}
.hi-john-parent {
  position: relative;
  width: 100%;
  height: 64px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  text-align: left;
  font-size: 23.28px;
  color: var(--mid-emphasized);
  font-family: var(--font-montserrat);
}
