.box,
.error-message,
.key-field {
  position: absolute;
}
.box {
  height: 100%;
  width: 100%;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  border-radius: var(--br-9xs);
  background-color: var(--base-white);
  border: 1px solid var(--color-gainsboro-100);
  box-sizing: border-box;
}
.key-field {
